import React, { useContext, useEffect, useState } from 'react';
import { Link, useNavigate, useLocation } from 'react-router-dom';
import { AuthContext } from '../../contexts/AuthContext';
import '../../styles/header.css';

export default function Header() {
    const { isLoggedIn, logout } = useContext(AuthContext);
    const navigate = useNavigate();
    const location = useLocation(); //   
    const [shrinkHeader, setShrinkHeader] = useState(false);
    const [showDropdown, setShowDropdown] = useState(false);
    const [showSubMenu, setShowSubMenu] = useState(false); //   

    const handleLogout = () => {
        logout();
        navigate('/');
    };

    useEffect(() => {
        const handleScroll = () => {
            if (window.scrollY > 50) {
                setShrinkHeader(true);
            } else {
                setShrinkHeader(false);
            }
        };

        window.addEventListener('scroll', handleScroll);

        return () => {
            window.removeEventListener('scroll', handleScroll);
        };
    }, []);

    //        
    const getActiveClass = (path) => {
        return location.pathname === path ? 'active-menu' : '';
    };

    return (
        <div className={`header ${shrinkHeader ? 'shrink' : ''}`}>
            <div className="top-header">
                <Link to="/" className="logo-link">adorable</Link>
                <div className="auth-links">
                    {isLoggedIn ? (
                        <button onClick={handleLogout} className="logout-button"></button>
                    ) : (
                        <Link to="/login" className="login-button"></Link>
                    )}
                </div>
            </div>
            <div className="sidebar">
                <nav className="navigation">
                    <ul className="nav-menu">
                        <li className={getActiveClass('/')}>
                            <Link to="/"></Link>
                        </li>
                        <li className={getActiveClass('/shelter')}>
                            <Link to="/shelter"> </Link>
                        </li>
                        <li className={getActiveClass('/adoft')}>
                            <Link to="/adoft"> </Link>
                        </li>
                        <li className={getActiveClass('/store')}>
                            <Link to="/store"></Link>
                        </li>
                        <li className={getActiveClass('/mypage')}>
                            <Link to="/mypage"></Link>
                        </li>
                    </ul>
                </nav>
                <div className="footer-links">
                    <a href="https://github.com/SSuaSea" target="_blank" rel="noopener noreferrer">
                         
                    </a>
                    <a href="https://github.com/SSuaSea" target="_blank" rel="noopener noreferrer">
                        GitHub
                    </a>
                </div>
            </div>
        </div>
    );
}
