import React from 'react';
import { Carousel } from 'react-responsive-carousel';
import 'react-responsive-carousel/lib/styles/carousel.min.css'; //    .
import '../styles/notice.css'; //   .

function Notice() {
    const notices = [
        {
            title: "   ",
            description: "          .",
            image: "https://github.com/SSuaSea/react-adorable/blob/main/public/image/dog5.jpg?raw=true" //    
        },
        {
            title: "  ",
            description: "          .",
            image: "https://github.com/SSuaSea/react-adorable/blob/main/public/image/dog6.jpg?raw=true" //    
        },
        {
            title: "   ",
            description: "     .   .",
            image: "https://github.com/SSuaSea/react-adorable/blob/main/public/image/dog7.jpg?raw=true" //    
        }
    ];

    return (
        <div className="notice">
            <Carousel showArrows={false} autoPlay={true} infiniteLoop={true} interval={3000} showThumbs={false}>
                {notices.map((notice, index) => (
                    <div key={index} className="notice-slide" style={{ backgroundImage: `url(${notice.image})` }}>
                        <div className="notice-content">
                            <span className="notice-label"></span>
                            <h2>{notice.title}</h2>
                            <p>{notice.description}</p>
                        </div>
                    </div>
                ))}
            </Carousel>
        </div>
    );
}

export default Notice;

