import React, { useEffect, useState } from 'react';
import { Link, useLocation, useNavigate } from 'react-router-dom';
import '../styles/store.css';
import storeData from '../data/storeData.js';

function Store({ initialCount }) {
    const [productData, setProductData] = useState([]);
    const [currentPage, setCurrentPage] = useState(1);
    const [totalPages, setTotalPages] = useState(1);
    const itemsPerPage = initialCount || 12; //  12 
    const pagesToShow = 5; //  
    const location = useLocation();
    const navigate = useNavigate();

    useEffect(() => {
        setProductData(storeData);
    }, []);

    // location.pathname        
    useEffect(() => {
        setCurrentPage(1);
        setProductData(storeData);
    }, [location.pathname]);

    //   
    const filterProductsByCategory = () => {
        const path = location.pathname;
        if (path.includes('/store/treat')) {
            return productData.filter(product => product.category === 'treat');
        } else if (path.includes('/store/supplies')) {
            return productData.filter(product => product.category === 'clothing');
        } else if (path.includes('/store/toys')) {
            return productData.filter(product => product.category === 'toys');
        } else if (path.includes('/store/bowl')) {
            return productData.filter(product => product.category === 'bowl');
        }
        return productData; //    
    };

    //       
    const filteredProducts = filterProductsByCategory();
    
    useEffect(() => {
        setTotalPages(Math.ceil(filteredProducts.length / itemsPerPage));
    }, [filteredProducts, itemsPerPage]);

    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = filteredProducts.slice(indexOfFirstItem, indexOfLastItem);

    const paginate = (pageNumber) => setCurrentPage(pageNumber);

    const renderPageNumbers = () => {
        let startPage = Math.max(currentPage - Math.floor(pagesToShow / 2), 1);
        let endPage = Math.min(startPage + pagesToShow - 1, totalPages);
    
        if (endPage - startPage + 1 < pagesToShow) {
            startPage = Math.max(endPage - pagesToShow + 1, 1);
        }
    
        const pageNumbers = [];
    
        if (currentPage > 1) {
            pageNumbers.push(
                <button key="prev" onClick={() => paginate(currentPage - 1)}>
                    &laquo;
                </button>
            );
        }
    
        for (let i = startPage; i <= endPage; i++) {
            pageNumbers.push(
                <button
                    key={i}
                    onClick={() => paginate(i)}
                    className={currentPage === i ? 'active' : ''}
                >
                    {i}
                </button>
            );
        }
    
        if (currentPage < totalPages) {
            pageNumbers.push(
                <button key="next" onClick={() => paginate(currentPage + 1)}>
                    &raquo;
                </button>
            );
        }
    
        return pageNumbers;
    };

    return (
        <div className="container">
            <div className="store">
                <div className="category-list-horizontal">
                    <button onClick={() => navigate('/store')} className={location.pathname === '/store' ? 'active' : ''}></button>
                    <button onClick={() => navigate('/store/treat')} className={location.pathname === '/store/treat' ? 'active' : ''}></button>
                    <button onClick={() => navigate('/store/bowl')} className={location.pathname === '/store/bowl' ? 'active' : ''}></button>
                    <button onClick={() => navigate('/store/clothing')} className={location.pathname === '/store/clothing' ? 'active' : ''}></button>
                    <button onClick={() => navigate('/store/toys')} className={location.pathname === '/store/toys' ? 'active' : ''}></button>
                </div>
    
                <div className="product-list">
                    {currentItems.map((product, index) => (
                        <div key={index} className="product-card">
                            <img className="product-image" src={product.image || 'placeholder.jpg'} alt={product.name} />
                            <h2>{product.name}</h2>
                            <p>: {product.price}</p>
                            <Link to={`/product/${product.id}`}></Link>
                        </div>
                    ))}
                </div>
    
                {totalPages > 1 && (
                    <div className="pagination">
                        {renderPageNumbers()} {/*    */}
                    </div>
                )}
            </div>
        </div>
    );
}

export default Store;
